import React from "react";
import Badge from "../../assets/badge.png";
import "../footer/styles/footer.css";

const Footer = () => {
  return (
    <footer className="footer">
      <div className="footer-container">
        <div className="footer-list">
          <FooterContent
            header={"Thng tin chung"}
            row1="Gii thiu Froggy Gaming"
            row2="Tin tc"
            row3="nh gi khch hng"
            row4="Lin h"
          ></FooterContent>
          <FooterContent
            header={"Chnh sch chung"}
            row1="Quy nh chung"
            row2="Chnh sch vn chuyn"
            row3="Chnh sch bo hnh"
            row4="Chnh sch i, tr li hng"
            row5="Chnh sch cho doanh nghip"
          ></FooterContent>
          <FooterContent
            header={"Thng tin khuyn mi"}
            row1="Sn phm bn chy"
            row2="Sn phm khuyn mi"
            row3="Hng thanh l"
          ></FooterContent>
          <FooterContent
            header={"Follow us! <3"}
            row1={<i className="fa-brands fa-facebook"></i>}
            row2={<i className="fa-brands fa-youtube"></i>}
            row3={<i className="fa-brands fa-tiktok"></i>}
            row4={<i className="fa-brands fa-instagram"></i>}
          ></FooterContent>
        </div>
        <div className="footer-storeinfo">
          <div className="footer-storeinfo-left">
            <h3>Cng ty TNHH Thng Mi Froggy Gaming</h3>
            <div className="footer-address">
              <p>
                Showroom HN: 8A Tn Tht Thuyt, M nh, Nam T Lim, H Ni /
                Tel: (096) 9277386 / (086) 830.2123
              </p>
              <p>
                Trung tm bo hnh : 8A Tn Tht Thuyt, M nh, Nam T Lim,
                H Ni / Tel: (096) 9277386 / (086) 830.2123
              </p>
            </div>
          </div>
          <div className="footer-storeinfo-right">
            <img src={Badge} alt="Badge" />
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;

function FooterContent({ header, row1, row2, row3, row4, ...props }) {
  return (
    <div className="footer-content">
      <ul className="footer-row">
        <h3>{header}</h3>
        <div className="footer-column">
          <li className="footer-item">{row1}</li>
          <li className="footer-item">{row2}</li>
          <li className="footer-item">{row3}</li>
          <li className="footer-item">{row4}</li>
          {props.row5 && <li className="footer-item">{props.row5}</li>}
        </div>
      </ul>
    </div>
  );
}
